<template>
  <div class="study page">
    <div class="study-header">
      <span v-for="nav in navs" :key="nav" :class="current === nav ? 'active' : ''" @click="selectNav(nav)">
        {{nav}}
      </span>
    </div>

    <div class="study-album">
      <div class="study-album-item" @click="toDetail(item.href, item.title)" :data-href="`https://${keyword}.org/${item.href}`" v-for="item in album" :key="item.title">
        <div class="study-album-item-cover">
          <img :src="`https://${keyword}.org/${item.cover}`" alt="">
        </div>
        <span>{{item.title}}</span>
      </div>
    </div>

    <div class="study-pagination">
      <span v-show="pagination.current > 1" ontouchstart="" @click="getAlbum(--pagination.current)">-</span>
      <span>{{pagination.current}} / {{pagination.total}}</span>
      <span ontouchstart="" @click="getAlbum(++pagination.current)">+</span>
    </div>
  </div>
</template>

<script>
import { $fetch } from '@/store'

const DOMAIN = 'https://jrainlau.com/api/hentai'

export default {
  data () {
    return {
      current: 'kt',
      navs: ['kt', 'wm', 'mt', 'jq', 'yz', 'zp', 'om', 'yd', 'mx', 'sm', 'ga'],
      keyword: '',
      album: []
    }
  },
  computed: {
    pagination () {
      const eg = this.album[0]
      const pag = {
        current: 0,
        total: 0
      }
      if (eg) {
        pag.current = eg.currentPage
        pag.total = eg.totalPages
      }
      return pag
    }
  },
  mounted () {
    document.body.querySelector('header').style.display = 'none'
    document.title = 'Study hard'
    this.keyword = localStorage.getItem('study_keyword')
    if (!this.keyword) {
      this.keyword = prompt('Set the keyword')
      this.keyword && localStorage.setItem('study_keyword', this.keyword) && this.getAlbum()
    } else {
      this.getAlbum()
    }
  },
  methods: {
    selectNav (nav) {
      if (this.current !== nav) {
        this.current = nav
        this.getAlbum()
      }
    },
    async getAlbum (index = 1) {
      const result = await $fetch({
        url: `${DOMAIN}/${this.keyword}`,
        method: 'post',
        data: {
          index,
          cate: 'art' + this.current
        }
      }).catch(e => e.response)
      this.album = result.data
      document.querySelector('.study').scrollTop = 0
    },
    toDetail (href, title) {
      const newRoute = this.$router.resolve({ path: `/study2?href=${href}&title=${title}` })
      window.open(newRoute.href, '_blank')
    }
  }
}
</script>

